* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    h1 {
      font-size: 32px;
      font-weight: 700;
    }

    h2 {
      font-size: 14px;
      font-weight: 400;
      color: #828282;
      margin-top: 24px;
      margin-left: 2px;
    }
  }
}
