$btn-color: #1240e5;

.availabilityBtn {
  padding: 10px;
  color: $btn-color;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid $btn-color;
  border-radius: 5px;
  transition-duration: 0.5s;

  &:hover {
    background-color: $btn-color;
    color: white;
  }
}
